.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  background-color: #F1FAEE;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-group-item {
  background-color: #F1FAEE;
  padding:14px;
}


